<template>
  <div>
    <van-cell-group :title="label">
      <van-cell
        v-for="employee in result"
        :key="employee.id"
        :value="employee.text"
      >
        <van-icon
          slot="right-icon"
          name="close"
          @click="deleteEmployee(employee)"
        />
      </van-cell>
      <van-cell :value="placeholder">
        <van-icon slot="right-icon" name="add-o" @click="employeeShow = true" />
      </van-cell>
    </van-cell-group>
    <van-popup
      v-model="employeeShow"
      position="right"
      :overlay="true"
      class="overlay-wrap"
    >
      <van-nav-bar
        :title="navbarTitle"
        left-text="返回"
        left-arrow
        @click-left="goBackFn"
      />
      <van-search placeholder="搜索" v-model="searchText" />
      <div class="list-wrap">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <van-checkbox-group v-model="resultChecked">
            <van-cell-group>
              <van-cell
                v-for="(item, index) in dataList"
                clickable
                :key="item.id"
                :title="item.text"
                @click="toggle(index)"
              >
                <van-checkbox :name="item" ref="checkboxes" />
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </van-list>
      </div>
      <van-button size="large" type="primary" @click="complateFn"
        >完成</van-button
      >
    </van-popup>
  </div>
</template>

<script>
import {
  Icon,
  Cell,
  CellGroup,
  Popup,
  NavBar,
  Search,
  List,
  Checkbox,
  CheckboxGroup,
  Button
} from "vant";
export default {
  name: "SearchField",
  components: {
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [Popup.name]: Popup,
    [NavBar.name]: NavBar,
    [Search.name]: Search,
    [List.name]: List,
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup
  },
  data() {
    return {
      searchText: "",
      employeeShow: false,
      employeeList: [],
      result: [],
      resultChecked: [],
      loading: false,
      finished: false
    };
  },
  props: {
    dataList: Array,
    label: String,
    placeholder: String,
    navbarTitle: String
  },
  methods: {
    goBackFn() {
      this.employeeShow = false;
    },
    onLoad() {
      setTimeout(() => {
        // 加载状态结束
        this.loading = false;
        this.finished = true;
      }, 500);
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    complateFn() {
      this.employeeShow = false;
      this.result = this.resultChecked;
    },
    deleteEmployee(employee) {
      var index = this.result.indexOf(employee);
      this.result.splice(index, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
.overlay-wrap {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  .list-wrap {
    background-color: #fff;
    flex: 1;
    width: 100%;
    height: 0;
    overflow-y: auto;
  }
}
</style>
