<script>
import SearchField from "../components/SearchField.vue";
import {
  Cell,
  CellGroup,
  Icon,
  Field,
  NumberKeyboard,
  Picker,
  DatetimePicker,
  NavBar,
  Tab,
  Tabs,
  Panel,
  Button,
  Actionsheet,
  Row,
  Col,
  Dialog,
  Collapse,
  CollapseItem
} from "vant";
import moment from "moment";
export default {
  name: "ItemDetail",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Field.name]: Field,
    [NumberKeyboard.name]: NumberKeyboard,
    [Picker.name]: Picker,
    [DatetimePicker.name]: DatetimePicker,
    [NavBar.name]: NavBar,
    [Panel.name]: Panel,
    [Dialog.name]: Dialog,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Actionsheet.name]: Actionsheet,
    [Button.name]: Button,
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    SearchField
  },
  data() {
    return {
      active: 0,
      comboxText: "",
      comboxValue: "",
      comboxShow: false,
      dateTime: moment(new Date()).format("YYYY-MM-DD"),
      currentTime: moment(new Date()).format("YYYY-MM-DD"),
      dateShow: false,
      minDate: new Date(),
      pickerData: [
        { text: "DFCO", value: 1 },
        { text: "ECN", value: 2 },
        { text: "设计变更工作流程", value: 3 },
        { text: "图文档审核流程", value: 4 },
        { text: "物料发布/修改申请", value: 5 },
        { text: "物料作废工作流程", value: 6 }
      ],
      dialogShow: false,
      dialogType: 0,
      message: "",
      dialogTitle: "",
      dialogPlaceholder: "",
      expandActives: ["1", "2", "3"],
      employeeData: {
        label: "创建者",
        placeholder: "添加人员",
        navbarTitle: "选择人员",
        dataList: [
          { id: 1, text: "彭勇" },
          { id: 2, text: "许国亮" },
          { id: 3, text: "杨同帅" },
          { id: 4, text: "施晓雪" },
          { id: 5, text: "张建" }
        ]
      },
      isEdit: false,
      showComfirmBottom: false
    };
  },
  methods: {
    goBackFn() {
      this.$router.push({
        name: "todo"
      });
    },
    pickerSubmit(value) {
      this.comboxText = value.text;
      this.comboxValue = value.value;
      this.comboxShow = false;
    },
    pickerDate(value) {
      this.dateTime = moment(value).format("YYYY-MM-DD");
      this.dateShow = false;
    },
    showDialog(dialogType) {
      if (dialogType === 1) {
        this.dialogTitle = "通过通知";
        this.dialogPlaceholder = "同意";
      } else if (dialogType === 2) {
        this.dialogTitle = "驳回通知";
        this.dialogPlaceholder = "驳回事由";
      }
      this.message = "";
      this.dialogType = dialogType;
      this.dialogShow = true;
    },
    dialogCloseFn(action, done) {
      if (action === "confirm") {
        done();
      } else {
        done();
      }
    },
    onEditFn() {
      this.isEdit = true;
    }
  },
  created() {
    const forward = this.$route.params.forward;
    if (forward === "todo") {
      this.showComfirmBottom = true;
    } else {
      this.showComfirmBottom = false;
    }
  }
};
</script>

